import React from 'react'
import { Alert, AlertIcon, AlertDescription, CloseButton, useDisclosure } from '@chakra-ui/react'

export default function Error() {
    return (
        <Alert margin='15px auto' pos='relative' top='0' borderRadius='5px' padding='5px' status='error' height='40px' width='315px' bg='red'>
            <AlertIcon height='20px' width='20px' />
            <AlertDescription ml='5px'> () /</AlertDescription>
        </Alert>
    )
}
