import React, { useState, useEffect } from "react";
import { Select, useDisclosure, SlideFade } from "@chakra-ui/react";
import { NavLink, useNavigate, useSearchParams, useLocation } from "react-router-dom";
import './Form.css'
import Weather from "../Weather/Weather";

export default function Form({error, setError}) {
    const apiKey = "63a4ff87a1b34b01195302d69494d684"
    const [city, setCity] = useState("")
    const [value, setValue] = useState("")
    const location = useLocation()
    const navig = useNavigate()
    let [searchParams] = useSearchParams()
    const [weather, setWeather] = useState({
        temp: undefined,
        main: undefined,
        city: undefined,
        country: undefined,
        pressure: undefined,
        wind: undefined
    });
    const handleSubmit = async (e) => {
        e.preventDefault();
        getWeather(e.target.value);
    };

    const handleChangeCity = (e) => {
        if ((e.target.value !== " ") && (e.target.value[e.target.value.length - 1] !== " ")) {
            setCity(e.target.value);
        }
    }
    const handleChangeInput = (e) => {
        const pm = document.getElementById("last_ct").options[document.getElementById("last_ct").selectedIndex].text
        setValue(e.target.value)
        if (pm !== " ") {
            setCity(pm)
            getWeather(pm)
            navig(`/my-app/?city=${pm}`)
        }
    }
    const addPrivate = async (e) => {
        let x = await fetch(
            `https://api.openweathermap.org/data/2.5/weather?q=${e.target.value}&appid=${apiKey}&units=metric&lang=ru`
        );
        if (x.status !== 404) {
            localStorage.setItem(localStorage.length + 1, e.target.value);
        }
    };
    const getWeather = async (locate) => {
        if (locate) {
            const apiUrl = await fetch(
                `https://api.openweathermap.org/data/2.5/weather?q=${locate}&appid=${apiKey}&units=metric&lang=ru`
            );
            if (apiUrl.status !== 404) {
                setError(false);
                const data = await apiUrl.json();
                setWeather({
                    temp: data.main.temp,
                    main: data.weather.map((e) => <span key={e.main}>{e.main}</span>),
                    city: data.name,
                    pressure: data.main.pressure,
                    country: data.sys.country,
                    wind: data.wind.speed
                });
            } else {
                setError(true);
            }
        }
    };

    const lst = [
        " ",
        localStorage.getItem(localStorage.length),
        localStorage.getItem(localStorage.length - 1),
        localStorage.getItem(localStorage.length - 2)
    ];

    const options = lst.map((text, index) => {
        return (
            <option key={index} value={index}>
                {text}
            </option>
        );
    });

    useEffect(() => {
        const findCity = searchParams.get("city");
        setCity(findCity);
        getWeather(findCity);
    }, []);

    useEffect(() => {
        const findCity = searchParams.get("city");
        setCity(findCity);
        getWeather(findCity);
    }, [location.search]);
    

    const currentUrl = window.location.href

    return (
        <>
            <div id="form">
                <form onSubmit={handleSubmit}>
                <input
                    type="text"
                    name="city"
                    id="ct_inp"
                    placeholder="/"
                    value={city}
                    onChange={handleChangeCity}
                />
                <NavLink to={`/my-app/?city=${city}`}>
                    <button id="prvt" value={city} onClick={addPrivate}>
                             
                    </button>
                </NavLink>
                </form>
                {localStorage.length > 0 && 
                <Select id="last_ct" onChange={handleChangeInput} value={value}>
                    {options}
                </Select>
                }
            </div> 
            <Weather
                temp={weather.temp}
                main={weather.main}
                city={weather.city}
                country={weather.country}
                pressure={weather.pressure}
                wind={weather.wind}
                currentUrl={currentUrl}
            />
        </>
    )
}
