import React from 'react'
import './Weather.css'
import { LinkIcon } from "@chakra-ui/icons";
import { TelegramShareButton, VKShareButton, WhatsappShareButton, TelegramIcon, VKIcon, WhatsappIcon} from "react-share";
import { Tooltip } from '@chakra-ui/react'

export default function Weather({temp, main, city, country, wind, pressure, currentUrl}) {
  return (
      <div id='weather'>
      { city &&  
        <div id='weather__container'>
          <div id='left-part'>
            <p id='title__city'>{city}, {country}</p>
            {main[0].key === "Clouds" && 
              <p>: </p>
            }
            {main[0].key === "Clear" && 
              <p>: </p>
            }
            {main[0].key === "Rain" && 
              <p>: </p>
            }
            {main[0].key === "Mist" && 
              <p>: </p>
            }
            {main[0].key === "Drizzle" && 
              <p>: </p>
            }
            {main[0].key === "Snow" && 
              <p>: </p>
            }
            {main[0].key === "Thunderstrom" && 
              <p>: </p>
            }
            <p>: {Math.round(temp)}</p>
            <p>: {wind} /</p>
            <p>: {Math.round(pressure / 1.33317)}  . .</p>
          </div>
          <div id='right-part'>
          {main[0].key === "Clouds" &&
            <img src={require("./cloud.png")} alt="1" />
          }
          {main[0].key === "Clear" &&
              <img src={require("./sun.png")} alt="2" />
          }
          {main[0].key === "Rain" &&
            <img src={require("./rain.png")} alt="3" />
          }
          {main[0].key === "Mist" &&
            <img src={require("./mist.png")} alt="3" />
          }
          {main[0].key === "Drizzle" &&
            <img src={require("./drizzle.png")} alt="4" />
          }
          {main[0].key === "Snow" &&
            <img src={require("./snowflake.png")} alt="4" />
          }
          {main[0].key === "Thunderstrom" &&
            <img src={require("./thunderstorm.png")} alt="1" />
          }
            <div id='links'>
              <div id='link'>
                <Tooltip id='tooltip' label="  Telegram">
                  <TelegramShareButton title={`    !`} url={`${currentUrl}`}>
                    <TelegramIcon size={32} borderRadius={50} id="link__icon"/>
                  </TelegramShareButton>                  
                </Tooltip>
              </div>
              <div id='link'>
                <Tooltip id='tooltip' label="  VK">
                  <VKShareButton title={`    !`} url={`${currentUrl}`}>
                    <VKIcon size={32} borderRadius={50} id="link__icon"/>
                  </VKShareButton>
                </Tooltip>
              </div>
              <div id='link'>
                <Tooltip id='tooltip' label="  WhatsApp">
                  <WhatsappShareButton title={`    !`} url={`${currentUrl}`}>
                    <WhatsappIcon size={32} borderRadius={50} id="link__icon"/>
                  </WhatsappShareButton>
                </Tooltip>
              </div>
            </div> 
          </div>
        </div>
      }
      </div>
  )
}
